
"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import type { ReactNode } from 'react';
import {
  SidebarProvider,
  Sidebar,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
  SidebarTrigger,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarMenuSub,
  SidebarMenuSubItem,
  SidebarMenuSubButton,
  SidebarInset,
  SidebarGroup,
  SidebarGroupLabel,
  useSidebar,
} from "@/components/ui/sidebar";
import { Header } from "@/components/core/header";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { APP_NAME, MAIN_NAV_LINKS, ADMIN_NAV_LINKS, SETTINGS_NAV_LINKS } from "@/lib/constants";
import type { NavItem } from "@/lib/constants";
import { Package, ChevronDown, ChevronRight } from "lucide-react";
import { cn } from "@/lib/utils";
import { useAuth } from "@/contexts/auth-context";
import React, { useState } from 'react';

function AppSidebar() {
  const pathname = usePathname();
  const { state } = useSidebar(); // For icon-only tooltips
  const [isAdminOpen, setIsAdminOpen] = useState(true);
  const { isAuthenticated } = useAuth();

  if (!isAuthenticated) {
    return null; // Don't render sidebar if not authenticated (should be handled by middleware anyway)
  }

  const renderNavItem = (item: NavItem, isSubItem = false) => {
    const isActive = item.match ? item.match(pathname) : pathname === item.href;
    const commonProps = {
      isActive,
      tooltip: state === "collapsed" ? item.label : undefined,
      className: "justify-start",
    };

    if (isSubItem) {
      return (
        <SidebarMenuSubItem key={item.href}>
          <Link href={item.href} passHref legacyBehavior>
            <SidebarMenuSubButton {...commonProps} isActive={isActive}>
              {item.label}
            </SidebarMenuSubButton>
          </Link>
        </SidebarMenuSubItem>
      );
    }

    return (
      <SidebarMenuItem key={item.href}>
        <Link href={item.href} passHref legacyBehavior>
          <SidebarMenuButton {...commonProps}>
            <item.icon className="h-4 w-4" />
            <span>{item.label}</span>
          </SidebarMenuButton>
        </Link>
      </SidebarMenuItem>
    );
  };
  
  return (
    <Sidebar collapsible="icon" variant="sidebar" side="left" className="border-r">
      <SidebarHeader className="p-4">
        <Link href="/" className="flex items-center gap-2">
          <Package className="h-6 w-6 text-primary" />
          <h1 className={cn("text-lg font-semibold", state === "collapsed" && "hidden")}>{APP_NAME}</h1>
        </Link>
      </SidebarHeader>
      <ScrollArea className="flex-grow">
        <SidebarContent className="p-2">
          <SidebarMenu>
            {MAIN_NAV_LINKS.map(item => !item.adminOnly ? renderNavItem(item) : null)}

            {/* Admin Section */}
            <SidebarMenuItem>
              <SidebarMenuButton
                className="justify-between"
                onClick={() => setIsAdminOpen(!isAdminOpen)}
                aria-expanded={isAdminOpen}
              >
                <div className="flex items-center gap-2">
                  <Package className="h-4 w-4" /> {/* Or use a specific Admin icon */}
                  <span>Admin</span>
                </div>
                {isAdminOpen ? <ChevronDown className="h-4 w-4" /> : <ChevronRight className="h-4 w-4" />}
              </SidebarMenuButton>
              {isAdminOpen && (
                <SidebarMenuSub>
                  {ADMIN_NAV_LINKS.map(item => renderNavItem(item, true))}
                </SidebarMenuSub>
              )}
            </SidebarMenuItem>
          </SidebarMenu>
        </SidebarContent>
      </ScrollArea>
      <SidebarFooter className="p-2 border-t">
         {SETTINGS_NAV_LINKS.map(item => renderNavItem(item))}
      </SidebarFooter>
    </Sidebar>
  );
}


export default function AppLayout({ children }: { children: ReactNode }) {
  const { isLoading, isAuthenticated } = useAuth();

  // Optional: Show a loading state while auth is being checked
  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <div className="h-16 w-16 animate-spin rounded-full border-4 border-primary border-t-transparent"></div>
      </div>
    );
  }
  
  // If not authenticated, middleware should redirect. This is a fallback.
  if (!isAuthenticated) {
     // In a real app, middleware handles this.
     // For client-side fallback, you might show a message or redirect.
     // useEffect(() => { router.push('/login'); }, [router]);
    return null; 
  }

  return (
    <SidebarProvider defaultOpen={true}>
      <AppSidebar />
      <SidebarInset className="flex flex-col min-h-screen">
        <Header />
        <main className="flex-1 overflow-y-auto p-4 md:p-8 bg-background">
          {children}
        </main>
      </SidebarInset>
    </SidebarProvider>
  );
}
